import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-view-user',
  templateUrl: './view-user.component.html',
  styleUrls: ['./view-user.component.css']
})
export class ViewUserComponent implements OnInit {
  viewUserDetail:any
  constructor(public service : DataService, private router: Router) { }

  ngOnInit() {
    let credential = {
      "userId":localStorage.getItem("userId")
 }
    this.service.postApi('/userDetail', credential).subscribe(response => {
      if (response['responseCode'] == 200) {
              console.log('success', response['data'])           
              this.viewUserDetail=response['data']
      } 
      else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
 this.service.error(response['responseMessage'])
 this.router.navigate(['/login'])
}
      else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  editUser() {
    this.router.navigate(['/edit-user'])
  }
  cancel() {
    this.router.navigate(['/user-management'])

  }
}



