import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data.service';

@Component({
  selector: 'app-product-management',
  templateUrl: './product-management.component.html',
  styleUrls: ['./product-management.component.css']
})
export class ProductManagementComponent implements OnInit {
  p: number = 1;
  searchProduct={data:""};
  sortProduct={gender:"",bodyType:""}
  product: any;
  total: any;
  gender = [


    {id:1, name:"Male"},
    {id:2, name:"Female"} 
  
  ]
  maleBodyType = [

    {id:1, name:"Slim Jim"},
    {id:2, name:"Muscle Man"},
    {id:3, name:"Big Guy"},
    {id:4, name:"Bulky"}
  ]

  femaleBodyType = [
    {id:1, name:"Rectangle"},
    {id:2, name:"Peer"},
    {id:3, name:"Triangle"},
    {id:4, name:"Hourglass"}
  ]
  selectedBody: any;
  constructor(private router: Router, public service: DataService) { }

  ngOnInit() {
    this.displayProduct()
  }
  addProduct() {
this.router.navigate(['/add-product'])
  }
  displayProduct() {
    let credential = {
      "page":this.p,
      "Search":this.searchProduct.data,
      "productGender":this.sortProduct.gender,
      "bodyType":this.sortProduct.bodyType
      
    }
    for (let val in credential) 
    {
        if (credential[val] == '') 
        {
            delete credential[val]
        }
      }
    this.service.postApi('/productList', credential).subscribe(response => {
    
      if (response['responseCode'] == 200) {
              console.log('success', response['data'].docs)           
              this.product=response['data'].docs
              this.total=response['data'].total
      } 
      else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
      }
      else if(response['responseCode'] == 403) {  
      this.service.error(response['responseMessage'])
      this.router.navigate(['/login'])
      }
      else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  sort() {
    this.displayProduct()
  }
  onSelect(productId) {
   this.sortProduct.bodyType = ""
   if(productId == "") {
      this.sortProduct.gender = ""
      this.sortProduct.bodyType = ""
      this.selectedBody = null
    }
    if(productId == "Male") {
      this.selectedBody = this.maleBodyType
    }
    else if(productId == "Female") {
      this.selectedBody = this.femaleBodyType
    }
    
}
changePage(page) 
{

    this.p = page
   
    this.displayProduct()  

}
}



