import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-brand-management',
  templateUrl: './brand-management.component.html',
  styleUrls: ['./brand-management.component.css']
})
export class BrandManagementComponent implements OnInit {
  addBrandForm: FormGroup;
  menBrandList=[]
  womenBrandList=[]
  bothBrandList=[]
  searchBrand={data:""};
  brandDetail:any={"gender": ""}
  menPage:number=1
  womenPage:number=1
  bothPage:number=1
  menTotal:any
  womenTotal:any
  bothTotal:any
  constructor(private formBuilder: FormBuilder, public service: DataService, private router: Router) { }

  ngOnInit() {
    
    this.addBrandForm = this.formBuilder.group({
      brandName: ['', Validators.compose([Validators.required, Validators.pattern(/^[a-zA-Z ]*$/), Validators.minLength(2)])]
      });
      this.displayBrand()
     
  }
 
  search() {
    this.displayBrand()
  }
  displayBrand() {

    let credential = {
     "search":this.searchBrand.data,
     "menPage":this.menPage,
     "womenPage":this.womenPage,
     "bothPage":this.bothPage
   }
    
    for (let val in credential) 
      {
          if (credential[val] == '') 
          {
              delete credential[val]
          }
      }
    this.service.postApi('/brandNameList', credential).subscribe(response => {
      
      if (response['responseCode'] == 200) {
              console.log('success', response['data'].menList)  


              this.menBrandList = response['data'].menList.men
            
              this.womenBrandList = response['data'].womenList.women
              this.bothBrandList = response['data'].bothList.both
              this.menTotal=response['data'].menList.menTotal
              this.womenTotal=response['data'].womenList.womenTotal
              this.bothTotal=response['data'].bothList.bothTotal
              
              
      } 
      else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
 this.service.error(response['responseMessage'])
 this.router.navigate(['/login'])
}
      else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
     })
  }
  seeMoreMan() {
this.menPage = this.menPage + 1
let credential = {
  "search":this.searchBrand.data,
  "menPage":this.menPage,
  "womenPage":this.womenPage,
  "bothPage":this.bothPage
}
 
 for (let val in credential) 
   {
       if (credential[val] == '') 
       {
           delete credential[val]
       }
   }
 this.service.postApi('/brandNameList', credential).subscribe(response => {
   if (response['responseCode'] == 200) {
           console.log('success', response['data'].menList)  
           
         
           for(let i=0;i<response['data'].menList.men.length;i++){
             this.menBrandList.push(response['data'].menList.men[i])
           
           }
           
          this.menTotal=response['data'].menList.menTotal
            
   } else {
     console.log('Failure', response['responseMessage']) 
   }
 }, error => {
   console.log('something went wrong') 
  })
  }
  seeMoreWoman() {
    this.womenPage = this.womenPage + 1
    let credential = {
      "search":this.searchBrand.data,
      "menPage":this.menPage,
      "womenPage":this.womenPage,
      "bothPage":this.bothPage
    }
     
     for (let val in credential) 
       {
           if (credential[val] == '') 
           {
               delete credential[val]
           }
       }
     this.service.postApi('/brandNameList', credential).subscribe(response => {
       if (response['responseCode'] == 200) {
               console.log('success', response['data'].womenList)  
               
             
               for(let i=0;i<response['data'].womenList.women.length;i++){
                 this.womenBrandList.push(response['data'].womenList.women[i])
               
               }
               
              this.womenTotal=response['data'].womenList.womenTotal
                
       } else {
         console.log('Failure', response['responseMessage']) 
       }
     }, error => {
       console.log('something went wrong') 
      })
      }
      seeMoreBoth() {
        this.bothPage = this.bothPage + 1
        let credential = {
          "search":this.searchBrand.data,
          "menPage":this.menPage,
          "womenPage":this.womenPage,
          "bothPage":this.bothPage
        }
         
         for (let val in credential) 
           {
               if (credential[val] == '') 
               {
                   delete credential[val]
               }
           }
         this.service.postApi('/brandNameList', credential).subscribe(response => {
           if (response['responseCode'] == 200) {
                   console.log('success', response['data'].menList)  
                   
                 
                   for(let i=0;i<response['data'].bothList.both.length;i++){
                     this.bothBrandList.push(response['data'].bothList.both[i])
                   
                   }
                   
                  this.bothTotal=response['data'].bothList.bothTotal
                    
           } else {
             console.log('Failure', response['responseMessage']) 
           }
         }, error => {
           console.log('something went wrong') 
          })
          }
  addBrand() {
    this.menPage = 1
    this.womenPage = 1
    this.bothPage = 1
    let credential = {
      "createdBy":localStorage.getItem("adminId"),
      "brandName":this.brandDetail.name,
      "brandGender":this.brandDetail.gender
      
      }
          this.service.postApi('/addNewBrand', credential).subscribe(response => {
           console.log(JSON.stringify(credential))
            if (response['responseCode'] == 200) {
                    this.service.success(response['responseMessage'])
                    console.log('success', response['responseMessage'])           
                    this.displayBrand()
            } else {
              this.service.error(response['responseMessage'])
              console.log('Failure', response['responseMessage']) 
            }
          }, error => {
            console.log('something went wrong') 
            
          })
          // this.brandDetail.name = ""
          // this.brandDetail.gender = ""
  }
}



