import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';
import {Location} from '@angular/common';
import { FormGroup, FormBuilder, Validators, MaxLengthValidator } from '@angular/forms';

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {
  editUserForm: FormGroup;
  editUserDetail:any
  selectedProduct:any
  selectedBody:any
  editBody:any
  condition:boolean=false
  male:string="Male"
  female:string="Female"
  editUser:any={"bodyType": "", "gender": ""}
  gender = [


  {id:1, name:"Male"},
  {id:2, name:"Female"}    

]
maleBodyType = [

  {id:1, name:"Slim Jim"},
  {id:2, name:"Muscle Man"},
  {id:3, name:"Big Guy"},
  {id:4, name:"Bulky"}
]

femaleBodyType = [
  {id:1, name:"Rectangle"},
  {id:2, name:"Peer"},
  {id:3, name:"Triangle"},
  {id:4, name:"Hourglass"}
]

  constructor(public service: DataService, private router: Router, private _location: Location,  private formBuilder: FormBuilder) { }

  ngOnInit() {
   this.editUserForm = this.formBuilder.group({
      name:   ['', Validators.compose([Validators.required, Validators.pattern(/^[^\s][a-zA-Z ]*$/), Validators.minLength(2)])],
      email:  ['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_]+([\.][A-Z0-9_]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,3})+$/i), Validators.maxLength(50)])],
      age:    ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])],
      height: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])],
      weight: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])]

    });
    let credential = {
    "userId":localStorage.getItem("userId")
 }
    this.service.postApi('/userDetail', credential).subscribe(response => {
      if (response['responseCode'] == 200) {
              console.log('success', response['data'])           
              this.editUserDetail=response['data']
              
              if(this.editUserDetail.gender == "Male") {
                this.editBody = this.maleBodyType
              }
              else if(this.editUserDetail.gender == "Female") {
                this.editBody = this.femaleBodyType
              }
              
              
      } else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  saveChanges() {
    let credential = {
      "userId":localStorage.getItem("userId"),
      "name":this.editUser.name,
      "email":this.editUser.email,
      "age":this.editUser.age,
      "bodyType":this.editUser.bodyType,
      "gender":this.editUser.gender,
      "height":this.editUser.height,
      "weight":this.editUser.weight,

 }
    this.service.postApi('/editUser', credential).subscribe(response => {
      console.log(JSON.stringify(credential))
      if (response['responseCode'] == 200) {
              console.log('success', response['responseMessage'])           
              this.router.navigate(['/user-management'])
      } else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  cancel() {
    this._location.back();
  }
    onSelect(productId) {
      this.editUser.bodyType=""
      
this.condition = true
      let maleBodyType = [

        {id:1, name:"Slim Jim"},
        {id:2, name:"Muscle Man"},
        {id:3, name:"Big Guy"},
        {id:4, name:"Bulky"}
      ]
      
      let femaleBodyType = [
        {id:1, name:"Rectangle"},
        {id:2, name:"Peer"},
        {id:3, name:"Triangle"},
        {id:4, name:"Hourglass"}
      ]
    this.selectedProduct = null;
    for (var i = 0; i < this.gender.length; i++)
    {
      if (this.gender[i].name == productId) {
        this.selectedProduct = this.gender[i];
       
      }
    }
    if(this.selectedProduct.name == "Male") {
     
      this.selectedBody = maleBodyType
    }
    else {
    
      this.selectedBody = femaleBodyType
    
    }
 this.editUser.bodyType = " "
}
 
}



