import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpHeaders } from '@angular/common/http';
declare var toastr:any
@Injectable()
export class DataService {
    token = localStorage.getItem("token")
  baseUrl = 'http://172.16.6.80:1468/admin'
//   baseUrl = 'http://ec2-52-76-162-65.ap-southeast-1.compute.amazonaws.com:1468/admin'
  getHttpOptions = {
    headers: new HttpHeaders({
        "Content-Type": "application/json",
        "token": this.token
    })
}
 
  constructor(public http: HttpClient) { }
getApi(url) {
    return this.http.get(this.baseUrl + url, this.getHttpOptions);
}
postApi(url, data) {
    return this.http.post(this.baseUrl + url, data, this.getHttpOptions);
}
error (title) {
    toastr.error(title)
}
success(title:string,meassage?:string) {
    toastr.success(title,meassage);
}
setting() {
    toastr.options = {
        "closeButton": true,
        "debug": false,
        "newestOnTop": false,
        "progressBar": false,
        "positionClass": "toast-bottom-right",
        "preventDuplicates": false,
        "onclick": null,
        "showDuration": "400",
        "hideDuration": "1000",
        "timeOut": "5000",
        "extendedTimeOut": "1000",
        "showEasing": "swing",
        "hideEasing": "linear",
        "showMethod": "fadeIn",
        "hideMethod": "fadeOut",
        'animate' : 'flyRight'

     }
}
ngOnInit() {
    this.setting()
}
}



