import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../data.service';
declare var $ :any;
@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {
 total:any
  p: number = 1;
  searchData={data:""};
  searchDataBody={gender:"",bodyType:""}
  userData=[]
  _id:any
  selectedProduct:any
  selectedBody:any
  gender = [


    {id:1, name:"Male"},
    {id:2, name:"Female"}    
  
  ]
  maleBodyType = [

    {id:1, name:"Slim Jim"},
    {id:2, name:"Muscle Man"},
    {id:3, name:"Big Guy"},
    {id:4, name:"Bulky"}
  ]

  femaleBodyType = [
    {id:1, name:"Rectangle"},
    {id:2, name:"Peer"},
    {id:3, name:"Triangle"},
    {id:4, name:"Hourglass"}
  ]
  constructor(private router: Router, public service : DataService) { }

  ngOnInit() {
    if(this.searchDataBody.gender == " ") {
      this.searchDataBody.bodyType = ""
    }
    this.displayUser()
    
  }
  onSelect(productId) {
    if(productId == "") {
      this.searchDataBody.gender = ""
      this.searchDataBody.bodyType = ""
      this.selectedBody = null
    }
    this.searchDataBody.bodyType = ""
    this.selectedProduct = null;
    for (var i = 0; i < this.gender.length; i++)
    {
      if (this.gender[i].name == productId) {
        this.selectedProduct = this.gender[i];
       
      }
    }
    if(this.selectedProduct.name == "Male") {
      this.selectedBody = this.maleBodyType
    }
    else {
      this.selectedBody = this.femaleBodyType
    
    }
 
}

  // addUser() {
  //   this.p = 1
  //    this.router.navigate(['/add-user'])
  // }
  delete(userId){
    this._id = userId
    $('#delete').modal('show')

  }
  yes() {
    let credential = {
      "userId":this._id
    }
    this.service.postApi('/deleteUser', credential).subscribe(response => {
      if (response['responseCode'] == 200) {
              this.service.success(response['responseMessage'])
              console.log('success', response['responseMessage'])
              $('#delete').modal('hide') 
              this.displayUser()
      } else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  cancelDelete() {
    $('#delete').modal('hide')
  }
  search() {
   this.displayUser()
  }
  sort() {
   this.displayUser()
  }
  view(id) {
  localStorage.setItem('userId',id)
  this.router.navigate(['/view-user'])
  }
  // edit(id) {
  //   localStorage.setItem('userId',id)
  // this.router.navigate(['/edit-user'])
  // }
  changePage(page) 
  {
  
      this.p = page
     
      this.displayUser()  
  
  }
displayUser() {

  let credential = {
    "page":this.p,
    "search":this.searchData.data,
    "gender":this.searchDataBody.gender,
    "bodyType": this.searchDataBody.bodyType

  }
  
  for (let val in credential) 
    {
        if (credential[val] == '') 
        {
            delete credential[val]
        }
    }
  this.service.postApi('/getAllUsers', credential).subscribe(response => {
    
    if (response['responseCode'] == 200) {
            console.log('success', response['data'].docs)           
            this.userData=response['data'].docs
            this.total=response['data'].total
            for(var i=0;i<this.userData.length;i++){
              if(this.userData[i].isSubscription == false){
                this.userData[i].isSubscription = 'UnPaid'
              }
              else{
                this.userData[i].isSubscription = 'Paid'
              }
            }
    } 
    else if(response['responseCode'] == 402) {
      this.service.error(response['responseMessage'])
      this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
this.service.error(response['responseMessage'])
this.router.navigate(['/login'])
}
    else {
      console.log('Failure', response['responseMessage']) 
    }
  }, error => {
    console.log('something went wrong') 
    
  })
}
}



