import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-style-management',
  templateUrl: './style-management.component.html',
  styleUrls: ['./style-management.component.css']
})
export class StyleManagementComponent implements OnInit {
  styleTips=[]
  searchStyle={data:""};
  searchStyleTip={gender:"",bodyType:"",brand:""}
  addStyleTip={"gender": "","bodyType":"","brand":""}
  p: number = 1;
  selectedBody: any
  selectedBodyAdd: any;
  gender = [


    {id:1, name:"Male"},
    {id:2, name:"Female"} 
  
  ]
  maleBodyType = [

    {id:1, name:"Slim Jim"},
    {id:2, name:"Muscle Man"},
    {id:3, name:"Big Guy"},
    {id:4, name:"Bulky"}
  ]

  femaleBodyType = [
    {id:1, name:"Rectangle"},
    {id:2, name:"Peer"},
    {id:3, name:"Triangle"},
    {id:4, name:"Hourglass"}
  ]
  selectedProduct: any;
  
  styleBrands: any;
  searchStyleBrands: any;
  selectedSearchProduct: { id: number; name: string; };
  total: any;
  
  constructor(public service: DataService, private router: Router) { }

  ngOnInit() {
   this.displayStyleTips()
  }
  
  displayStyleTips() {
    let credential = {
      "page":this.p,
      "search":this.searchStyle.data,
      "styleGender":this.searchStyleTip.gender,
      "bodyType":this.searchStyleTip.bodyType,
      "brandName":this.searchStyleTip.brand
      
    }
    for (let val in credential) 
    {
        if (credential[val] == '') 
        {
            delete credential[val]
        }
    }
    this.service.postApi('/styleTipList', credential).subscribe(response => {
     
      if (response['responseCode'] == 200) {
              console.log('success', response['data'].docs) 
              this.styleTips=response['data'].docs
              this.total=response['data'].total
      } 
      else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
 this.service.error(response['responseMessage'])
 this.router.navigate(['/login'])
}
      else {
        this.service.error('Invalid Email')
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }
  changePage(page) 
  {
      this.p = page
     
      this.displayStyleTips()  
  
  }
  search() {
    this.displayStyleTips()
   }
   sort() {
     this.displayStyleTips()
   }
   onSelectSearch(productId) {
    this.searchStyleTip.bodyType = ""
    this.searchStyleTip.brand = ""
    if(productId == "") {
       this.searchStyleTip.gender = ""
       this.searchStyleTip.bodyType = ""
       this.searchStyleTip.brand = ""
       this.selectedBody = null
       this.searchStyleBrands = null
     }
     if(productId == "Male") {
       this.selectedBody = this.maleBodyType
     }
     else if(productId == "Female") {
       this.selectedBody = this.femaleBodyType
     }
     let credential = {
       "styleGender":productId
     }
     for (let val in credential) 
     {
         if (credential[val] == '') 
         {
             delete credential[val]
         }
     }
     this.service.postApi('/styleBrandList', credential).subscribe(response => {
       if (response['responseCode'] == 200) {
               console.log('success', response['data'])
               this.searchStyleBrands=response['data']
       } 
       else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
 this.service.error(response['responseMessage'])
 this.router.navigate(['/login'])
}
       else {
         console.log('Failure', response['responseMessage']) 
       }
     }, error => {
       console.log('something went wrong') 
       
     })
 }


 onSelectSearchBody(productId) {
  this.searchStyleBrands = null
  let credential = {
    
    "bodyType":productId
  }
  for (let val in credential) 
  {
      if (credential[val] == '') 
      {
          delete credential[val]
      }
  }
  
  this.service.postApi('/styleBrandList', credential).subscribe(response => {
    if (response['responseCode'] == 200) {
            
            console.log('success', response['data'])
            this.searchStyleBrands=response['data']
            
    } 
    else if(response['responseCode'] == 402) {
      this.service.error(response['responseMessage'])
      this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
this.service.error(response['responseMessage'])
this.router.navigate(['/login'])
}
    else {
      console.log('Failure', response['responseMessage']) 
    }
  }, error => {
    console.log('something went wrong') 
    
  })
 }


   onSelectAdd(productId) {
     this.addStyleTip.bodyType = ""
     this.addStyleTip.brand = ""
   if(productId == "") {
      this.addStyleTip.gender = ""
      this.addStyleTip.bodyType = ""
      this.addStyleTip.brand = ""
      this.selectedBodyAdd = null
      this.styleBrands = null
    }
    if(productId == "Male") {
      this.selectedBodyAdd = this.maleBodyType
    }
    else if(productId == "Female") {
      this.selectedBodyAdd = this.femaleBodyType
    }
   
    
    let credential = {
      "productGender":productId,
      "bodyType":this.addStyleTip.bodyType
    }
    for (let val in credential) 
    {
        if (credential[val] == '') 
        {
            delete credential[val]
        }
    }
    this.service.postApi('/bodyTypeBrandList', credential).subscribe(response => {
      
      if (response['responseCode'] == 200) {
              console.log('success', response['data']) 
              this.styleBrands=response['data']
      }
      else if(response['responseCode'] == 402) {
        this.service.error(response['responseMessage'])
        this.router.navigate(['/login'])
}
else if(response['responseCode'] == 403) {  
 this.service.error(response['responseMessage'])
 this.router.navigate(['/login'])
}
      else {
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
}
  addStyle() {
    this.p = 1
    let credential = {
      "createdBy":localStorage.getItem("adminId"),
      "styleGender":this.addStyleTip.gender,
      "brandName":this.addStyleTip.brand,
      "bodyType":this.addStyleTip.bodyType,
     }
          this.service.postApi('/addNewStyleTip', credential).subscribe(response => {
           console.log(JSON.stringify(credential))
            if (response['responseCode'] == 200) {
                    this.service.success(response['responseMessage'])
                    console.log('success', response['responseMessage'])           
                    this.displayStyleTips()
            } 
            else if(response['responseCode'] == 402) {
              this.service.error(response['responseMessage'])
              this.router.navigate(['/login'])
     }
     else if(response['responseCode'] == 403) {  
       this.service.error(response['responseMessage'])
       this.router.navigate(['/login'])
     }
            else {
              this.service.error(response['responseMessage'])
              console.log('Failure', response['responseMessage']) 
            }
          }, error => {
            console.log('something went wrong') 
            
          })
          this.addStyleTip.gender = ""
          this.addStyleTip.brand = ""
          this.addStyleTip.bodyType = ""
  }
}



