import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { DataService } from '../data.service';
import { ToastrService } from 'ngx-toastr';
declare var $: any

@Component ({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  isSubmitted: boolean = false;
  loginData:any={}
  rememberMeEmail:any
  rememberMePassword:any
  rememberMeCheck:any
  token: any;
  constructor(private router: Router, private formBuilder: FormBuilder, public service : DataService, private toastr: ToastrService) { }

  ngOnInit() {
   this.loginForm = this.formBuilder.group({
      email:['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_]+([\.][A-Z0-9_]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,3})+$/i), Validators.maxLength(50)])],
      password:['', Validators.compose([Validators.required,  Validators.maxLength(16), Validators.minLength(8)])],
      rememberMe:[]
    });
    $(function() {
      $('#email,#password').on('keypress', function(e) {
          if (e.which == 32)
              return false;
      });
    });
    this.loginData.email = localStorage.getItem("email")
    this.loginData.password = localStorage.getItem("password")
    this.loginData.rememberMe = true
  }
  
  login() {
    let credential = {
      "email":this.loginData.email,
      "password": this.loginData.password
    }
    this.service.postApi('/login', credential).subscribe(response => {
     
      if (response['responseCode'] == 200) {
              console.log('success', response['responseMessage']) 
              localStorage.setItem("adminId",response['data']._id)
              localStorage.setItem("token",response['data'].token)
              if(this.loginData.rememberMe == true) {
                localStorage.setItem('email',this.loginData.email)
                localStorage.setItem('password',this.loginData.password)
              }
             
              this.router.navigate(['/dashboard'])
      } else {
        this.service.error('Invalid Email')
        console.log('Failure', response['responseMessage']) 
      }
    }, error => {
      console.log('something went wrong') 
      
    })
  }

}



