import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { DataService } from '../data.service';

declare var $: any

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {
  addUserForm: FormGroup;
  userDetail:any={"bodyType": "", "gender": ""}
  selectedProduct:any
  selectedBody:any
  
  gender = [


    {id:1, name:"Male"},
    {id:2, name:"Female"}    
  
  ]

  maleBodyType = [

    {id:1, name:"Slim Jim"},
    {id:2, name:"Muscle Man"},
    {id:3, name:"Big Guy"},
    {id:4, name:"Bulky"}
  ]

  femaleBodyType = [
    {id:1, name:"Rectangle"},
    {id:2, name:"Peer"},
    {id:3, name:"Triangle"},
    {id:4, name:"Hourglass"}
  ]
  

  onSelect(productId) {
    this.userDetail.bodyType=""
    let maleBodyType = [

      {id:1, name:"Slim Jim"},
      {id:2, name:"Muscle Man"},
      {id:3, name:"Big Guy"},
      {id:4, name:"Bulky"}
    ]
    
    let femaleBodyType = [
      {id:1, name:"Rectangle"},
      {id:2, name:"Peer"},
      {id:3, name:"Triangle"},
      {id:4, name:"Hourglass"}
    ]
    this.selectedProduct = null;
    for (var i = 0; i < this.gender.length; i++)
    {
      if (this.gender[i].name == productId) {
        this.selectedProduct = this.gender[i];
       
      }
    }
    if(this.selectedProduct.name == "Male") {
      this.selectedBody = this.maleBodyType
    }
    else {
      this.selectedBody = this.femaleBodyType
    
    }
 
}
constructor(private router: Router, private formBuilder: FormBuilder, public service : DataService) { }
  ngOnInit() {
      this.addUserForm = this.formBuilder.group({
      name: ['', Validators.compose([Validators.required, Validators.pattern(/^[^\s][a-zA-Z ]*$/), Validators.minLength(2)])],
      email:['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_]+([\.][A-Z0-9_]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,3})+$/i), Validators.maxLength(50)])],
      age: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])],
      height: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])],
      weight: ['', Validators.compose([Validators.required, Validators.pattern(/^[1-9][0-9]{1,7}$/)])],
      

    });
   
  }

  cancel() {
    this.router.navigate(['/user-management'])
  }
  save() {
    let credential = {
      "name":this.userDetail.name,
      "email":this.userDetail.email,
      "age":this.userDetail.age,
      "bodyType":this.userDetail.bodyType,
      "height":this.userDetail.height,	
      "weight":this.userDetail.weight,
      "gender":this.userDetail.gender
      
      }
          this.service.postApi('/addNewUser', credential).subscribe(response => {
           
            if (response['responseCode'] == 200) {
              this.service.success(response['responseMessage'])
                    console.log('success', response['responseMessage'])           
                    this.router.navigate(['/user-management'])
            } else {
              this.service.error('Fill the necessary fields.')
              console.log('Failure', response['responseMessage']) 
            }
          }, error => {
            console.log('something went wrong') 
            
          })
  }
}



