import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { SideMenuComponent } from './side-menu/side-menu.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UserManagementComponent } from './user-management/user-management.component';
import { AddUserComponent } from './add-user/add-user.component';
import { BrandManagementComponent } from './brand-management/brand-management.component';
import { ProductManagementComponent } from './product-management/product-management.component';
import { AddProductComponent } from './add-product/add-product.component';
import { StyleManagementComponent } from './style-management/style-management.component';
import { ViewUserComponent } from './view-user/view-user.component';
import { EditUserComponent } from './edit-user/edit-user.component';


const routes: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full' },
  {path: 'login', component: LoginComponent},
  {path: 'side-menu', component: SideMenuComponent},
  {path: 'dashboard', component: DashboardComponent},
  {path: 'user-management', component: UserManagementComponent},
  // {path: 'add-user', component: AddUserComponent},
  {path: 'brand-management', component: BrandManagementComponent},
  {path: 'product-management', component: ProductManagementComponent},
  {path: 'add-product', component: AddProductComponent},
  {path: 'style-management', component: StyleManagementComponent},
  {path: 'view-user', component: ViewUserComponent},
  // {path: 'edit-user', component: EditUserComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



